@import url(https://fonts.googleapis.com/css?family=Roboto:300,300i,400,700);
#rosters {
	width: 210px;
	height: calc( (1vh * 100) - 20px );
	height: calc( (var(--vh, 1vh) * 100) - 20px );
	display: flex;
	justify-content: flex-end;
	align-items: center;
	position: fixed;
	right: 0;
	top: 10px;
	transform: translateX(210px);
	z-index: 1000;
	transition: all 0.3s ease-in-out;
}
#rosters.isVisible {
	transform: translateX(0px);
}
.rosters_container {
	width: 100%;
	height: calc(1vh * 100);
	height: calc(var(--vh, 1vh) * 100);
	overflow-y: hidden;
	display: flex;
	flex-direction: column;
	align-items: flex-end;
}
.rosters_top_container {
	width: 100%;
}
.rosters_title {
	color: var(--white);
	background: var(--corruption);
	width: 200px;
	border: 2px solid var(--corruption);
	display: flex;
	justify-content: center;
	align-items: center;
	height: 40px;
	border-radius: 3px;
	margin: 10px 0 0 0;
	opacity: 0;
	visibility: hidden;
	transition: all 0.3s ease-in-out;
}
#rosters.isVisible .rosters_title {
	opacity: 1;
	visibility: visible;
}
.rosters_hover_button {
	position: absolute;
	width: 40px;
	height: 40px;
	left: -50px;
	top: 0;
	background: var(--corruption);
	color: var(--white);
	border: 2px solid var(--corruption);
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 3px;
	transition: all 0.3s ease-in-out;
}
#rosters.isVisible .rosters_hover_button {
	background: var(--danger-color);
	color: var(--white);
	border: 2px solid var(--danger-color);
}
.rosters_hover_button button {
	width: 40px;
	height: 40px;
}
.rosters_hover_button i {
	color: var(--white);
}
.rosters_list {
	width: 200px;
	height: calc(100% - 120px);
	background: var(--dark_blue);
	border: 2px solid var(--light);
	overflow-y: scroll;
	margin: 10px 10px 0 10px; 
	border-radius: 3px;
	opacity: 0;
	visibility: hidden;
	transition: all 0.3s ease-in-out;
}
#rosters.isVisible .rosters_list {
	opacity: 1;
	visibility: visible;
}
.roster_search {
	background: var(--dark_blue);
	margin: 10px 10px 0 10px; 
	width: calc(100% - 10px);
	border: 2px solid var(--light);
	border-radius: 3px;
	height: 40px;
}
#rosters .roster_search input {
	border: 0;
	height: 40px;
	width: calc(100% - 40px);
	color: var(--w);
}
#rosters .roster_search i {
	color: var(--light);
	height: 40px;
	width: 40px;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 1em;
}
#rosters .roster_search i.clean {
	color: var(--danger-color);
	cursor: pointer;
}
.rosters_list > li {
	width: 100%;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	height: 40px;
}
.rosters_list > li.xs-hide {
	display: none;
}
.rosters_list > li button {
	width: inherit;
	height: inherit;
	text-align: left;
	padding: 0;
	border-radius: 0;
	color: var(--white);
	background: var(--dark_blue);
}
.rosters_list > li button h6 {
	width: inherit;
	height: inherit;
	display: flex;
	justify-content: space-between;
	align-items: center;
	align-content: center;
	flex-wrap: wrap;
	padding: 0 10px;
}
.rosters_list > li button h6 div {
	width: 100%;
	font-size: 10px;
	color: var(--light);
}
.rosters_list > li.active_roster button {
	background: var(--middle_blue);
	color: var(--white);
}
.rosters_list > li button:hover {
	background: var(--middle_blue);
	color: var(--white);
}
.rosters_list > li:first-child > button {
	border-top: 0;
}
#covenant {
	width: 100%;
	padding: 40px 0;
}
.covenant_title {
	color: var(--gold);
	margin-bottom: 20px;
	text-align: center;
}
.covenant_title h2 {
	display: flex;
	align-items: flex-end;
	justify-content: center;
	margin-bottom: 10px;
}
.covenant_title h2 > span {
	display: block;
	width: calc(50% - 40px);
}
.covenant_title h2 > span:nth-child(1) {
	text-align: right;
}
.covenant_title h2 > span:nth-child(3) {
	text-align: left;
}
.covenant_title p {
	color: var(--w);
	font-size: 1rem;;
}
.covenant_title_image {
	width: 60px;
	height: 60px;
	background: #f00;
	margin-left: 10px;
	margin-right: 10px;
	display: flex;
	align-items: center;
	justify-content: center;
}
.covenant_title_image[data-chosen-covenant="Kyrian"] {
	background: url(data:image/png;base64,);
}
.covenant_title_image[data-chosen-covenant="Venthyr"] {
	background: url(data:image/png;base64,);
}
.covenant_title_image[data-chosen-covenant="Night Fae"] {
	background: url(data:image/png;base64,);
}
.covenant_title_image[data-chosen-covenant="Necrolord"] {
	background: url(data:image/png;base64,);
}
.covenant_tab_container {
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}
.covenant_description_container {
	position: relative;
	width: 100%;
	margin-top: 10px;
	background: var(--middle_blue);
	border: 2px solid var(--light);
	min-height: 200px;
}

@media screen and (max-width: 990px) {
	.covenant_tab_container {
		max-width: 340px;
		margin: 0 auto;
	}
}
.covenant_description {
    width: calc(100% - 40px);
    max-width: 700px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    text-align: center;
    opacity: 0;
}
.covenant_description.active {
    opacity: 1;
}
.covenant_description > .covenant_title {
    color: var(--white);
    margin-bottom: 10px;
}
.covenant_description > .covenant_text {
    color: var(--gold);
}
@media screen and (max-width: 767px) {
    .covenant_description {
        width: calc(100% - 20px);
    }
}
.covenant_tab_container > .covenant_tab {
    margin-right: 5px;
    margin-left: 5px;
    cursor: pointer;
    position: relative;
}
.covenant_tab_container > .covenant_tab > img {
    width: 50px;
    height: 50px;
    margin-bottom: -4px;
    border-radius: 50%;
    border: 2px solid var(--light);
}
.covenant_tab_container > .covenant_tab.active > img {
    border: 2px solid var(--gold);
}
.covenant_tab_container > .covenant_tab > div {
    width: 26px;
    height: 26px;
    position: absolute;
    z-index: 2;
    right: 1px;
    bottom: 0px;
    border-radius: 50%;
}
.covenant_tab_container > .covenant_tab > div[data-cocket-type="POTENCY"] ,
.covenant_tab_container > .covenant_tab > div[data-cocket-type="ENDURANCE"] ,
.covenant_tab_container > .covenant_tab > div[data-cocket-type="FINESSE"] {
    background: var(--light);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}
.covenant_tab_container > .covenant_tab.active > div[data-cocket-type="POTENCY"] ,
.covenant_tab_container > .covenant_tab.active > div[data-cocket-type="ENDURANCE"] ,
.covenant_tab_container > .covenant_tab.active > div[data-cocket-type="FINESSE"] {
    background: var(--gold);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}
.covenant_tab_container > .covenant_tab > div[data-cocket-type="POTENCY"] {
    background-image: url(/static/media/swords.b512a8e5.png) !important;
}
.covenant_tab_container > .covenant_tab > div[data-cocket-type="ENDURANCE"] {
    background-image: url(/static/media/shield.2ae64d2c.png) !important;
}
.covenant_tab_container > .covenant_tab > div[data-cocket-type="FINESSE"] {
    background-image: url(/static/media/flash.c2cdaff7.png) !important;
}

@media screen and (max-width: 990px) {
    .covenant_tab_container > .covenant_tab {
        margin-right: 10px;
        margin-left: 0;
    }
    .covenant_tab_container > .covenant_tab:last-child {
        margin-right: 0;
    }
    .covenant_tab_container > .covenant_tab > img {
        width: 35px;
        height: 35px;
    }
    .covenant_tab_container > .covenant_tab > div {
        width: 18px;
        height: 18px;
    }
}
@media screen and (max-width: 370px) {
    .covenant_tab_container > .covenant_tab > img {
        width: 30px;
        height: 30px;
    }
}
#armory {
    width: 100%;
    min-height: calc(1vh * 100);
    min-height: calc(var(--vh, 1vh) * 100);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: relative;
}
.armory_character_background {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow:hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
}
.armory_character_background img {
    min-width: 100%;
    min-height: 100%;
    object-position: center calc(50% - 100px);
    object-fit: cover;
}
#armory:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 200px;
    background: linear-gradient(to bottom, rgba(255,255,255,0) 0%, var(--dark_blue) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#0f091b',GradientType=0 );
    z-index: 1;
}
#armory .wrapper {
    position: relative;
    z-index: 2;
    padding: 60px 0;
}
.armory_items_container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    width: 100%;
}
.armory_items_container_left,
.armory_items_container_right,
.armory_items_container_center {
    width: calc( (100% - 20px) / 3 );
}
.armory_items_container_center {
    height: 100%;
    min-height: 560px;
}
.armory_items_container_left ul > li,
.armory_items_container_right ul > li {
    height: 60px;
    width: 100%;
    margin-bottom: 10px;
}
.armory_items_container_weapon {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.armory_items_container_weapon {
    margin-top: 60px;
}
.armory_items_container_weapon ul {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.armory_items_container_weapon ul > li {
    height: 60px;
    width: calc( (100% - 20px) / 3 );
}
.armory_items_container_weapon ul > li:first-child {
    margin-right: 10px;
}
@media screen and (max-width: 990px) {
    .armory_character_background img {
        object-position: center calc(50% - 50px);
        object-fit: cover;
        height: calc(100% + 100px);
        width: auto;
    }
    .armory_items_container_left ul > li,
    .armory_items_container_right ul > li {
        height: 40px;
        width: 40px;
    }
    .armory_items_container_weapon ul > li {
        height: 40px;
        width: 40px;
    }
    .armory_items_container_left,
    .armory_items_container_right{
        width: 40px;
    }
    .armory_items_container_center {
        width: calc(100% - 80px);
        height: 100%;
        min-height: 400px;
    }
    .armory_items_container_weapon{
        margin-top: 20px;
    }
}
.armory_items_container {
	position: relative;
}
.armory_item {
	cursor: pointer;
}
.armory_item_view {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	position: relative;
}
.armory_item_view .armory_item_view_info_addition {
	display: flex;
	justify-content: flex-start;
	align-items: center;
}
.armory_item_view .armory_item_view_info_addition > * > * {
	margin-right: 5px;
}
.armory_item_view_info_addition_enchantment,
.armory_item_view_info_addition_sockets {
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.armory_item_view_info_addition_item_level {
	color: var(--light)
}

/* float left */
.armory_items_container_left ul > li .armory_item_view .armory_item_view_info_addition > * > * ,
.armory_items_container_weapon ul > li:last-child .armory_item_view .armory_item_view_info_addition > * > * {
	margin-right: 5px;
	margin-left: 0;
}
.armory_items_container_weapon ul > li:last-child .armory_item_view > .armory_item_view_img,
.armory_items_container_left ul > li .armory_item_view > .armory_item_view_img {
	margin-right: 20px;
	margin-left: 0;
}
.armory_items_container_left ul > li .armory_item_view ,
.armory_items_container_weapon ul > li:last-child .armory_item_view {
	justify-content: flex-start;
}
.armory_items_container_left ul > li .armory_item_view .armory_item_view_info_addition,
.armory_items_container_weapon ul > li:last-child .armory_item_view .armory_item_view_info_addition {
	justify-content: flex-start;
	flex-direction: row;
}
.armory_items_container_left ul > li .armory_item_view > .armory_item_view_img,
.armory_items_container_weapon ul > li:last-child .armory_item_view > .armory_item_view_img {
	order: 1;
}
.armory_items_container_left ul > li .armory_item_view > .armory_item_view_info,
.armory_items_container_weapon ul > li:last-child .armory_item_view > .armory_item_view_info {
	order: 2;
}
.armory_items_container_left ul > li .armory_item_view .armory_item_view_info_title,
.armory_items_container_weapon ul > li:last-child .armory_item_view .armory_item_view_info_title {
	text-align: left;
}

/* float right */
.armory_items_container_right ul > li .armory_item_view .armory_item_view_info_addition > * > *,
.armory_items_container_weapon ul > li:first-child .armory_item_view .armory_item_view_info_addition > * > * {
	margin-right: 0;
	margin-left: 5px;
}
.armory_items_container_right ul > li .armory_item_view > .armory_item_view_img,
.armory_items_container_weapon ul > li:first-child .armory_item_view > .armory_item_view_img {
	margin-right: 0;
	margin-left: 20px;
}
.armory_items_container_right ul > li .armory_item_view,
.armory_items_container_weapon ul > li:first-child .armory_item_view {
	justify-content: flex-end;
}
.armory_items_container_right ul > li .armory_item_view .armory_item_view_info_addition,
.armory_items_container_weapon ul > li:first-child .armory_item_view .armory_item_view_info_addition {
	justify-content: flex-start;
	flex-direction: row-reverse;
}
.armory_items_container_right ul > li .armory_item_view > .armory_item_view_img,
.armory_items_container_weapon ul > li:first-child .armory_item_view > .armory_item_view_img {
	order: 2;
}
.armory_items_container_right ul > li .armory_item_view > .armory_item_view_info,
.armory_items_container_weapon ul > li:first-child .armory_item_view > .armory_item_view_info {
	order: 1;
}
.armory_items_container_right ul > li .armory_item_view .armory_item_view_info_title,
.armory_items_container_weapon ul > li:first-child .armory_item_view .armory_item_view_info_title {
	text-align: right;
}
@media screen and (max-width: 990px) {
	.armory_item_view_info{
		display: none;
	}
}
