.raid_progress {
	width: calc(100% - 10px);
	border-radius: 3px;
	border: 2px solid var(--light);
	background: var(--middle_blue);
	color: var(--w);
	min-height: 200px;
	margin: 0 5px;
}
.raid_progress_image {
	width: 100%;
	height: 200px;
	background: var(--middle_blue);
}
.raid_progress_image img {
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: center center;
}
.raid_progress_title {
	padding: 10px 20px;
	text-align: center;
}
.raid_progress_container {
	padding: 0 20px 20px 20px;
}
@media screen and (max-width: 990px) {
	.raid_progress_title {
		padding: 10px 5px;
		text-align: center;
	}
	.raid_progress_container {
		padding: 0 10px 10px 10px;
	}
}
.raid_progress_line{
	width: 100%;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	flex-wrap: wrap;
	cursor: pointer;
}
.raid_progress_container > .raid_progress_line:not(:last-child) {
	margin-bottom: 10px;
}
.raid_progress_line_title {
	width: 70px;
	color: var(--gold);
}
.raid_progress_line_container {
	width: calc(100% - 70px);
	background: var(--black);
	height: 30px;
	border-radius: 3px;
	border: 2px solid var(--light);
	overflow: hidden;
	position: relative;
}
.raid_progress_line_container_done {
	position: absolute;
	z-index: 1;
	display: block;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
}
.raid_progress_line_container_line {
	width: 20%;
	height: 28px;
	background: var(--corruption);
	display: flex;
	justify-content: center;
	align-items: center;
}
.raid_progress_line_container_line.zero {
	width: 0;
}
.raid_progress_line_hover_container {
	height: 0;
	opacity: 0;
	margin-top: 0;
	padding: 0;
	overflow-x: hidden;
	width: calc(100% - 70px);
	margin-left: 70px;
	display: flex;
	justify-content: flex-end;
	align-items: center;
	flex-wrap: wrap;
	border: 0px solid var(--light);
	background: var(--dark_blue);
	color: #0f0;
	border-radius: 3px;
}
.raid_progress_line_hover_container.active_details {
	height: auto;
	opacity: 1;
	margin-top: 5px;
	padding: 5px;
	border: 2px solid var(--light);
}
@media screen and (max-width: 767px) {
	.raid_progress_line_hover_container {
		width: 100%;
		margin-left: 0;
	}
}
.raid_progress_line_hover{
	width: 100%;
}
