/* var */
:root {
    /* color */
    /* Bootstrap Colors */
    --danger-color: #ff4444;
    --danger-color-dark: #cc0000;
    --warning-color: #ffbb33;
    --warning-color-dark: #ff8800;
    --success-color: #00c851;
    --success-color-dark: #007e33;
    --info-color: #33b5e5;
    --info-color-dark: #0099cc;

    /* MDB Colors */
    --default-color: #2bbbad;
    --default-color-dark: #00695c;
    --primary-color: #4285f4;
    --primary-color-dark: #0d47a1;
    --secondary-color: #aa66cc;
    --secondary-color-dar: #9933cc;

    /* Dark Theme */
    --elegant-color: #2e2e2e;
    --elegant-color-dark: #212121;
    --stylish-color: #4b515d;
    --stylish-color-dark: #3e4551;
    --unique-color: #3f729b;
    --unique-color-dark: #1c2331;
    --special-color: #37474f;
    --special-color-dark: #263238;

    /* Primary Palette */
    --darkblue: #142b44;
    --navblue: #1d508d;
    --lpblue: #297cbb;
    --Linkblue: #228ad6;
    --teal: #0fdebd;
    --green: #16c98d;
    --yellow: #feef6d;
    --orange: #ffc83f;
    --red: #fa5e5b;
    --plum: #bf538d;

    /* main text color full name*/
    --black: rgb(0, 0, 0);
    --dark: rgb(38, 38, 38);
    --chrome: rgb(56, 56, 56);
    --gray: rgb(104, 104, 104);
    --silver: rgb(148, 148, 148);
    --light: rgb(192, 192, 192);
    --subwhite: rgb(236, 236, 236);
    --white: rgb(255, 255, 255);

    /* main text color short name */
    --b: rgb(0, 0, 0);
    --d: rgb(38, 38, 38);
    --c: rgb(56, 56, 56);
    --g: rgb(104, 104, 104);
    --s: rgb(148, 148, 148);
    --l: rgb(192, 192, 192);
    --sw: rgb(236, 236, 236);
    --w: rgb(255, 255, 255);

    /* WOW color */
    --legendary: #ff8000;
    --epic: #c600ff;
    --rare: #0081ff;
    --uncommon: #1eff00;
    --common: #ffffff;
   
    --corruption: #956dd1;
    --dark_blue: #0f0918;
    --middle_blue: #231D2F;
    --green: #00ff00;
    --gold: #f8b700;
    --yellow: #feef6d;

    --haste: #0ed59b;
    --crit: #e01c1c;
    --mastery: #9256ff;
    --versality: #bfbfbf;

    /* material shadow */
    --shadow_1: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
    --shadow_2: 0 5px 11px 0 rgba(0, 0, 0, 0.18), 0 4px 15px 0 rgba(0, 0, 0, 0.15);
    --shadow_3: 0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    --shadow_4: 0 12px 15px 0 rgba(0, 0, 0, 0.24), 0 17px 50px 0 rgba(0, 0, 0, 0.19);
    --shadow_5: 0 16px 28px 0 rgba(0, 0, 0, 0.22), 0 25px 55px 0 rgba(0, 0, 0, 0.21);
    --shadow_6: 0 27px 24px 0 rgba(0, 0, 0, 0.2), 0 40px 77px 0 rgba(0, 0, 0, 0.22);

    /* weight */
    --bold: 700;
    --normal: 500;
    --thin: 300;
}

/* style & grid */
body {
    font-size: 14px;
    line-height: 1.2em;
    font-family: "Roboto", sans-serif;
    color: rgb(56, 56, 56);
    color: var(--chrome);
    background: rgb(255, 255, 255);
    background: var(--white);
    font-weight: 500;
    font-weight: var(--normal);
    width: 100vw;
    height: 100vh;
    height: calc(1vh * 100);
    height: calc(var(--vh, 1vh) * 100);
}
@media screen and (max-width: 1199px) {
    body {
        font-size: 12px;
    }
}
.page {
    width: 100vw;
    height: 100vh;
    height: calc(1vh * 100);
    height: calc(var(--vh, 1vh) * 100);
    overflow-x: auto;
    display: flex;
    flex-direction: column;
}
footer {
    margin-top: auto;
}
input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
    font-weight: 500;
    font-weight: var(--normal);
    font-size: 1em;
    line-height: 1.2em;
    color: rgb(56, 56, 56);
    color: var(--chrome);
    border: none;
    border-radius: 3px;
    background: none;
    padding: 0;
    font-family: "Roboto", sans-serif;
    cursor: pointer;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}
