.armory_item_hover_sockets > .armory_item_hover_socket:not(:last-child){
	margin-bottom: 5px;
}
.armory_item_hover_socket {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	font-size: 0.9em;
	color: var(--white);
}
.armory_item_hover_socket > div {
	width: 15px;
	height: 15px;
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
	margin-right: 5px;
}
.armory_item_hover_socket > div:after {
	content: "";
	width: 100%;
	height: 100%;
	position: absolute;
	z-index: 2;
	background: url(../../../../../../img/sockets.png) -60px 0 no-repeat;
}
.armory_item_hover_socket > div > img {
	width: 15px;
	height: 15px;
	border-radius: 5px;
}